
import React from 'react'
import { OrderHistoryTable } from '@loopring-web/component-lib'
import { WithTranslation, withTranslation } from 'react-i18next'
import { Box, Paper } from '@material-ui/core'
import styled from '@emotion/styled'
import { useGetTxs } from '../TxPanel/hooks'

const StylePaper = styled(Box)`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    flex: 1;
    background-color: ${({ theme }) => theme.colorBase.background().default};
    border-radius: ${({ theme }) => theme.unit}px;
    padding: 20px;

    .title {
        font-family: Gilroy-Medium;
        font-size: ${({ theme }) => theme.unit * 3}px;
        line-height: 19px;
    }

    .tableWrapper {
        display: flex;
        flex: 1;
        margin-top: 20px;
        border: 1px solid ${({ theme }) => theme.colorBase.borderColor};
        border-radius: ${({ theme }) => theme.unit}px;
        padding: 26px;

        .rdg {
            flex: 1;
        }
    }
` as typeof Paper;

const OrderPanel = withTranslation('common')((rest: WithTranslation) => {
    const container = React.useRef(null);
    const [pageSize, setPageSize] = React.useState(10);
    React.useEffect(() => {
        // @ts-ignore
        let height = container?.current?.offsetHeight;
        if (height) {
            setPageSize(Math.floor((height - 120) / 44) - 2);
        }
    }, [container, pageSize]);

    const { txs } = useGetTxs()

    return (
        <>
            <StylePaper ref={container}>
                <div className="title">Order History</div>
                <div className="tableWrapper">
                    <OrderHistoryTable {...{
                        pagination: {
                            pageSize: pageSize
                        },
                        rawData: [],
                        ...rest
                    }} />
                </div>
            </StylePaper>
        </>
    )
})

export default OrderPanel
