import { AmmPanelType } from '@loopring-web/component-lib';
import { AmmDetailBase } from '@loopring-web/component-lib/src/static-resource';
import * as fm from 'loopring-sdk';
import { BIG10 } from '../../defs/swap_defs';

export const ammPairInit = ({pair, _ammCalcData,ammType,tokenMap, coinMap, walletMap,ammMap ,ammpoolsbalance}:any)=>{
    _ammCalcData.coinInfoMap = coinMap;
    if(ammpoolsbalance){
        const baseVol = (fm.toBig(ammpoolsbalance.pooled[0].volume).div(BIG10.pow(tokenMap[pair.coinAInfo.simpleName].decimals)))
        const quoteVol = (fm.toBig(ammpoolsbalance.pooled[1].volume).div(BIG10.pow(tokenMap[pair.coinBInfo.simpleName].decimals)))
        _ammCalcData.AtoB =  quoteVol.div(baseVol).toNumber()
    }
    if(pair.coinAInfo){
        if(ammType === AmmPanelType.Deposit ) {
            _ammCalcData.myCoinA = {
                belong: pair.coinAInfo.simpleName,
                balance: walletMap ? walletMap[ pair.coinAInfo.simpleName ]?.count : 0,
            }
            _ammCalcData.myCoinB = {
                belong: pair.coinBInfo.simpleName,
                balance: walletMap ? walletMap[ pair.coinBInfo.simpleName ]?.count : 0,
            }
        }else {
            const key = `${pair.coinAInfo.simpleName}-${pair.coinBInfo.simpleName}`;
            let coinACount = 0,coinBCount = 0 ;
            if(walletMap){
                const  balance =  walletMap ? walletMap[ 'LP-' + key ]?.count : 0;
                const {totalLPToken,totalA,totalB}:AmmDetailBase<any> = ammMap[ 'AMM-' + key ];
                if(totalA && totalLPToken &&totalB) {
                    coinACount = totalA / totalLPToken * balance;
                    coinBCount = totalB / totalLPToken * balance;
                }
            }
            _ammCalcData.myCoinA = {
                belong: pair.coinAInfo.simpleName,
                balance: coinACount,
            }
            _ammCalcData.myCoinB = {
                belong: pair.coinBInfo.simpleName,
                balance: coinBCount,
            }
        }
    }

    // myCoinA:IBData<T>,
    // myCoinB:IBData<T>,
    // AtoB:number,
    // coinInfoMap: CoinMap<T, CoinInfo<T>>,
    // walletMap: WalletMap<T, WalletCoin<T>>,
    // AmmWalletMap: WalletMap<T, WalletCoin<T>>,
    //     slippage: number | string
    // slippageTolerance: Array<number | string>,
    //     fee: string,
    return  _ammCalcData
}
